@use '../../../assets/styles/color' as color;
@use '../../../assets/styles/mixin';

.modalContainer {
    /*display*/
    display: flex;
    /*box model*/
    border-bottom: 1px solid #e6ecf0;
    padding: 16px 23px;
  }
.left {
    /*display*/
    display: flex;
    flex-direction: column;
    align-items: center;
    /*box model*/
    margin-right: 10px;
    .avatarContainer {
      /*box model*/
      height: 50px;
      width: 50px;
      border-radius: 50%;
      overflow: hidden;
    }
    .tweetAvatar {
        /*box model*/
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.right {
  /*display*/
  display: flex;
  flex-direction: column;
  /*box model*/
  margin-bottom: 8px;
  /*other*/
  font-size: 16px;

  .tweetUserInfo {
    @include mixin.font-setting(16px, 700, color.$gray-10);
    .tweetUserAccount {
        /*box model*/
        margin-left: 8px;
        /*other*/
        color: #657786;
        @include mixin.font-setting(14px, 400, color.$secondary-color);
    }
  }
  .tweetContent {
      /*box model*/
      margin: 9px 0;
      line-height: 26px;
  }
  .replyAddress {
      /*box model*/
      margin: 15px 0 8px 0;
      font-size: 13px;
      .replyWord {
          /*other*/
          color: #657786;
      }
      .replyAccount {
          /*other*/
          color: color.$brand-color;
      }
  }
}

